import React from 'react';
import { Link } from 'react-router-dom'

class LinkItem extends React.Component{
  render(){
    return(
      <li className={this.props.className}>
          <Link to = {'/templete/' + this.props.id} >
            <img src = {this.props.img_src} alt = {this.props.title}  className="c-img"/>
            <div className = "u-padding">
              <h3 className = "c-text_white u-marginbottom0">{this.props.title}</h3>
            </div>          
          </Link>
      </li>
      );
  }
}

export default LinkItem;
