import React from 'react';
import ChipsAnimation from '../../Templetes/Contents/ChipsAnimation.js';

/* タイトル部分 */
class Header extends React.Component{
  constructor() {
    super();
  }
  render(){
    return(
      
      <div className = "f-background f-background_winered c-scrollsnap_item  l-centering">
        <ChipsAnimation text="MyPortfolio"
                        text_size="5vw"
                        wrapper_width="80vw"
                        wrapper_height="50px"
                        chip_count={150}
                        chip_width={5}
                        chip_height={5} />
      </div>
      
    );
  }
}

export default Header;
